import React, { useState, useEffect } from "react";
import "bootstrap/dist/css/bootstrap.min.css";
import { Modal as BootstrapModal } from "bootstrap";

const Modal = ({ product, onClose }) => {
  const [modalOpened, setModalOpened] = useState(false);
  console.log("handleCloseModal", onClose());
  useEffect(() => {
    // const handleKeyDown = (event) => {
    //     if (event.keyCode === 27) {
    //       onClose();
    //     }
    //   };

    const modalElement = document.getElementById("exampleModal");
    const modal = new BootstrapModal(modalElement);

    // const modalOpened = localStorage.getItem('modalOpened');

    //   if (!modalOpened) {
    //     modal.show();
    //     localStorage.setItem('modalOpened', true);
    //   }

    //   return () => {
    //     modal.hide();
    //   };
    // }, []);
    if (!modalOpened) {
      console.log("modalOpened:", modalOpened);
      modal.show();
      setModalOpened(true);
    }
    console.log("modalOpened:", modalOpened);

    return () => {
      modal.hide();
    };
  }, [modalOpened]);

  //   modal.show();
  //   // document.addEventListener('keydown', handleKeyDown);

  //   return () => {
  //       modal.hide();
  //       // document.removeEventListener('keydown', handleKeyDown);
  //   };
  // }, []);

  return (
    <>
      <div
        className="modal fade product_modal"
        data-bs-backdrop="static"
        aria-labelledby="staticBackdropLabel"
        aria-hidden="true"
        id="exampleModal"
        tabIndex="-1"
        role="dialog"
      >
        <div
          className="modal-dialog modal-lg modal-dialog-scrollable"
          role="document"
        >
          <div className="modal-content">
            <div className="modal-header">
              <h3 className="modal-title">{product.name}</h3>
              {/* <button
                type="button"
                className="btn-close text-white  custom-close-btn"
                data-bs-dismiss="modal"
                aria-label="Close"
                onClick={onClose}
                data-bs-theme="dark"
              ></button> */}
              <button type="button"
              data-bs-dismiss="modal"
                aria-label="Close"
                style={{color:"white !important"}}
                onClick={onClose}
                 className="close custom-close-btn small-tiny-description"
                        data-dismiss="modal">
                        Ã—
                    </button>
            </div>
            <div className="modal-body">
              <div className="row align-items-start justify-content-center">
                <div className="col-md-4">
                  <div className="product_image_section">
                    <img
                      src={product.Image}
                      alt="dummy image"
                      className="pro-image"
                    />
                  </div>
                </div>
                <div className="col-md-8">
                  {product?.product_code ? (
                    <>
                      <p className=" custom-list-style text-darkessblue small-tiny-description">
                        Product Code :{" "}
                        <span className="text-black">
                          {product?.product_code}
                        </span>
                      </p>
                    </>
                  ) : (
                    ""
                  )}
                  {product?.SKU > 0 ? (
                    <>
                      <p className="custom-list-style text-darkessblue small-tiny-description">
                        SKU : <span className="text-black">{product?.SKU}</span>
                      </p>
                    </>
                  ) : (
                    ""
                  )}

                  <p className="custom-list-style text-darkessblue small-tiny-description">
                    {product?.features?.name}
                  </p>
                  <ul>
                    {product.features.items.map((feature, index) => (
                      <li
                        className="text-black"
                        style={{ listStyle: "circle" }}
                        key={index}
                      >
                        {feature}
                      </li>
                    ))}
                  </ul>
                  {product?.indications_for_use?.name.length === 0 ? (
                    ""
                  ) : (
                    <>
                      <p className="custom-list-style text-darkessblue small-tiny-description">
                        {product?.indications_for_use?.name}
                      </p>
                    </>
                  )}

                  {product?.indications_for_use?.name !== "" &&
                  product?.indications_for_use.items?.length > 0 ? (
                    <>
                      <ul>
                        {product?.indications_for_use?.items.map(
                          (indication, index) => (
                            <li
                              className="text-black"
                              style={{ listStyle: "circle" }}
                              key={index}
                            >
                              {indication}
                            </li>
                          )
                        )}
                      </ul>
                    </>
                  ) : (
                    ""
                  )}
                  {/* {product?.measurement_instructions?.name?.length === 0 ? (
                    ""
                  ) : (
                    <>
                      <p className="text-darkessblue small-tiny-description">
                        {product?.measurement_instructions?.name}
                      </p>
                    </>
                  )}

                  {product?.measurement_instructions?.value !== "" &&
                  product?.measurement_instructions?.value?.length > 0 ? (
                    <>
                      <p>{product?.measurement_instructions?.value}</p>
                    </>
                  ) : (
                    ""
                  )} */}

                  {/* {product.measurement_instructions.size_options.some(
                    (option) => option.name !== "" || option.value !== ""
                  ) ? (
                    <ul>
                    <div className="row">
                      {product.measurement_instructions.size_options.map(
                        (option, index) => {
                          if (option.name !== "" || option.value !== "") {
                            return (
                              <div className="col-md-4 col-6">
                              <li style={{ listStyle: "circle" }} key={index}>
                                {option.name} : {option.value}
                              </li>
                              </div>
                            );
                          }
                          return null;
                        }
                      )}
                    </div>
                    </ul>
                  ) : (
                    ""
                  )} */}
                </div>
              </div>

              <div className="row">
                <div className="col-12">
                  {product?.measurement_instructions?.name?.length === 0 ? (
                    ""
                  ) : (
                    <>
                      <p className="custom-list-style text-darkessblue small-tiny-description">
                        {product?.measurement_instructions?.name}
                      </p>
                    </>
                  )}

                  {product?.measurement_instructions?.value !== "" &&
                  product?.measurement_instructions?.value?.length > 0 ? (
                    <>
                      <p>{product?.measurement_instructions?.value}</p>
                    </>
                  ) : (
                    ""
                  )}
                </div>
                <div className="col-12">
                  <div className="table-responsive">
                    {product.measurement_instructions.size_options.some(
                      (option) => option.name !== "" || option.value !== ""
                    ) ? (
                      <table className="table-bordered border-primary w-100">
                        <thead>
                          <tr>
                            <th>Size</th>
                            {product.measurement_instructions.size_options.map(
                              (option, index) => {
                                if (option.name !== "" || option.value !== "") {
                                  return <th key={index}>{option.name}</th>;
                                }
                                return null;
                              }
                            )}
                          </tr>
                        </thead>
                        <tbody>
                          <tr>
                            <td>CMS</td>
                            {product.measurement_instructions.size_options.map(
                              (option, index) => {
                                if (option.name !== "" || option.value !== "") {
                                  return <td key={index}>{option.value}</td>;
                                }
                                return null;
                              }
                            )}
                          </tr>
                        </tbody>
                      </table>
                    ) : (
                      ""
                    )}
                  </div>
                </div>
              </div>
              {/* <p>Available Stock: {product.available_stock}</p> */}
            </div>
            <div className="modal-footer my-1 py-0">
              <button
                type="button"
                className="btn btn-secondary"
                data-bs-dismiss="modal"
                onClick={onClose}
              >
                Close
              </button>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default Modal;
